@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("fonts/OpenSans-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("fonts/OpenSans-Bold.ttf") format("truetype");
}
@media (min-width: 992px) {
  :root {
    --block-gap-horizontal: 48px;
  }
}
@media (min-width: 1200px) {
  :root {
    --element-gap-horizontal: 48px;
    --element-gap-vertical: 48px;
    --block-gap-horizontal: 80px;
    --block-gap-vertical: 80px;
  }
}
html {
  box-sizing: border-box;
  cursor: default;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
*,
::after,
::before {
  box-sizing: inherit;
}
body {
  font-family: "Open Sans", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  line-height: 1.5rem;
  color: #222;
  background-color: #fff;
  min-width: 320px;
  overflow-y: scroll;
  scroll-behavior: smooth;
  font-display: swap;
  hanging-punctuation: first allow-end last;
  margin: 0;
}
h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}
h1,
.h1 {
  font-family: "Open Sans", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 2.5rem;
  line-height: 3rem;
  font-weight: bold;
}
@media (max-width: 767px) {
  h1,
  .h1 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
}
h2,
.h2 {
  font-family: "Open Sans", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 2.5rem;
  line-height: 3rem;
  font-weight: bold;
}
@media (max-width: 767px) {
  h2,
  .h2 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
}
h3,
.h3 {
  font-family: "Open Sans", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.75rem;
  line-height: 2.25rem;
  font-weight: bold;
}
@media (max-width: 767px) {
  h3,
  .h3 {
    font-size: 1.5rem;
    line-height: 2rem;
  }
}
ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}
figure {
  margin: 0 auto;
}
blockquote {
  margin: 0;
}
sup,
sub {
  line-height: 1em;
  font-size: 0.75em;
}
a,
.link {
  cursor: pointer;
  color: #0884ff;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
  transition-property: color;
}
a:hover,
.link:hover {
  color: #00376e;
}
a {
  text-decoration: none;
}
a:not([href]),
a[href^="tel"] {
  color: inherit;
  cursor: default;
}
a[href^="tel"] {
  white-space: nowrap;
}
button,
[role="button"],
[type="button"],
[type="reset"],
[type="submit"] {
  cursor: pointer;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: inherit;
  text-transform: inherit;
  color: inherit;
  text-align: left;
  padding: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
}
button[disabled],
[role="button"][disabled],
[type="button"][disabled],
[type="reset"][disabled],
[type="submit"][disabled] {
  cursor: initial;
}
input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="search"],
select,
textarea {
  display: block;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  padding: 0;
  border: 0;
  background-color: transparent;
  margin: 0;
  appearance: none;
}
input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder,
input[type="tel"]::placeholder,
input[type="search"]::placeholder,
select::placeholder,
textarea::placeholder {
  opacity: 1;
}
input[type="text"]::-ms-clear,
input[type="email"]::-ms-clear,
input[type="password"]::-ms-clear,
input[type="tel"]::-ms-clear,
input[type="search"]::-ms-clear,
input[type="text"]::-ms-reveal,
input[type="email"]::-ms-reveal,
input[type="password"]::-ms-reveal,
input[type="tel"]::-ms-reveal,
input[type="search"]::-ms-reveal {
  display: none;
}
input[type="text"]::-webkit-search-decoration,
input[type="email"]::-webkit-search-decoration,
input[type="password"]::-webkit-search-decoration,
input[type="tel"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration,
input[type="text"]::-webkit-search-cancel-button,
input[type="email"]::-webkit-search-cancel-button,
input[type="password"]::-webkit-search-cancel-button,
input[type="tel"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-cancel-button,
input[type="text"]::-webkit-search-results-button,
input[type="email"]::-webkit-search-results-button,
input[type="password"]::-webkit-search-results-button,
input[type="tel"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-button,
input[type="text"]::-webkit-search-results-decoration,
input[type="email"]::-webkit-search-results-decoration,
input[type="password"]::-webkit-search-results-decoration,
input[type="tel"]::-webkit-search-results-decoration,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}
input[type="number"] {
  appearance: textfield;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
}
select {
  cursor: pointer;
}
select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000;
}
select::-ms-expand {
  display: none;
}
textarea {
  resize: none;
}
img,
video {
  max-width: 100%;
  height: auto;
  display: block;
}
svg,
use {
  transform: translateZ(0);
  fill: currentColor;
  stroke: currentColor;
  display: block;
}
hr {
  border: 0;
  height: 1px;
  margin: 0;
  width: 100px;
  background-color: rgba(0, 0, 0, 0.18);
}
.btn {
  font-size: 1rem;
  line-height: 1.5rem;
  color: #fff;
  background-color: #0884ff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
  padding: 16px 28px;
  border-radius: 8px;
  white-space: nowrap;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
  transition-property: background-color;
}
.btn:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(8, 132, 255, 0.5);
}
.btn:not([disabled]):hover {
  background-color: #00376e;
}
.btn:not([disabled]):active {
  transform: translateY(1px);
}
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
  box-shadow: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.icon {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 1.5rem;
  vertical-align: top;
}
.page {
  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh, 1vh) * 100);
  overflow-x: hidden;
}
.header {
  padding-top: 16px;
  padding-bottom: 16px;
  padding-right: var(--block-gap-horizontal, 24px);
  padding-left: var(--block-gap-horizontal, 24px);
}
@media (min-width: 768px) {
  .header {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}
.header__wrap {
  max-width: 1200px;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .header__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-right: calc(var(--element-gap-horizontal, 24px) * -1);
    margin-bottom: calc(var(--element-gap-vertical, 40px) * -1);
  }
}
@media (min-width: 768px) {
  .header__fold,
  .header__collapse {
    width: auto;
    border: 0 solid transparent;
    border-right-width: var(--element-gap-horizontal, 24px);
    border-bottom-width: var(--element-gap-vertical, 40px);
  }
}
.header__menu {
  display: flex;
  align-items: center;
}
.header__menu--collapse {
  margin-right: -24px;
  margin-bottom: -24px;
}
.header__menu--collapse li {
  white-space: nowrap;
  margin-right: 24px;
  margin-bottom: 24px;
}
@media (max-width: 767px) {
  .header__menu--collapse {
    align-items: flex-start;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin-right: auto;
  }
}
.header__phone {
  margin-right: 24px;
}
@media (min-width: 768px) {
  .header__phone {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__collapse {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #fff;
    padding: 16px 24px;
    opacity: 0;
    visibility: hidden;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
    transition-property: opacity, transform;
    z-index: 100;
    overflow-y: auto;
  }
  .header__collapse--open {
    opacity: 1;
    visibility: visible;
  }
}
.header__toggle {
  line-height: 0;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}
.header__close {
  float: right;
}
@media (min-width: 768px) {
  .header__close {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__nav {
    margin-top: 32px;
  }
}
.main {
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
}
.scene {
  width: 100%;
  padding-top: var(--block-gap-vertical, 48px);
  padding-right: var(--block-gap-horizontal, 24px);
  padding-bottom: var(--block-gap-vertical, 48px);
  padding-left: var(--block-gap-horizontal, 24px);
}
.scene--full {
  padding-right: 0;
  padding-left: 0;
}
.grid {
  width: 100%;
}
.grid + .grid {
  margin-top: var(--element-gap-vertical, 40px);
}
.grid__wrap {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
.grid__list {
  display: flex;
  flex-wrap: wrap;
  margin-right: calc(var(--element-gap-horizontal, 24px) * -1);
  margin-bottom: calc(var(--element-gap-vertical, 40px) * -1);
}
.grid__list + .grid__list {
  margin-top: var(--element-gap-vertical, 40px);
}
.grid__item {
  width: 100%;
  border: 0 solid transparent;
  border-right-width: var(--element-gap-horizontal, 24px);
  border-bottom-width: var(--element-gap-vertical, 40px);
}
@media (min-width: 768px) {
  .grid__list--double .grid__item {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .grid__list--triple .grid__item {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .grid__list--triple .grid__item {
    width: 33.33%;
  }
}
@media (min-width: 768px) {
  .grid__list--quadruple .grid__item {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .grid__list--quadruple .grid__item {
    width: 33.33%;
  }
}
.wysiwyg {
  overflow-wrap: break-word;
}
.wysiwyg > * + * {
  margin-top: 1em;
}
.wysiwyg > h2 + *,
.wysiwyg > *:not(h2):not(h3):not(p):not(ul):not(ol) + *,
.wysiwyg > * + *:not(h2):not(h3):not(p):not(ul):not(ol) {
  margin-top: 2em;
}
.wysiwyg > * + h2 {
  margin-top: var(--common-element-gap-vertical, 40px) !important;
}
.wysiwyg > * + h3 {
  margin-top: 1em !important;
}
.wysiwyg > ul li + li,
.wysiwyg > ol li + li,
.wysiwyg > ul ul,
.wysiwyg > ol ul,
.wysiwyg > ul ol,
.wysiwyg > ol ol {
  margin-top: 0.5em;
}
.wysiwyg > p + ul,
.wysiwyg > p + ol {
  margin-top: 0.5em;
}
.wysiwyg > ul li {
  margin-left: 1.5em;
}
.wysiwyg > ul li::before {
  margin-left: -1.5em;
  position: absolute;
  content: "\2014";
  color: #ccc;
}
.wysiwyg > ol {
  counter-reset: code;
}
.wysiwyg > ol li {
  margin-left: 1.5em;
}
.wysiwyg > ol li::before {
  margin-left: -1.5em;
  position: absolute;
  content: counters(code, ".") ". ";
  counter-increment: code;
  color: #ccc;
}
.wysiwyg > ol ol {
  counter-reset: code;
}
.wysiwyg > ol li li {
  margin-left: 2.3em;
}
.wysiwyg > ol li li::before {
  margin-left: -2.3em;
}
.breadcrumbs {
  padding-top: 40px;
  padding-right: var(--block-gap-horizontal, 24px);
  padding-left: var(--block-gap-horizontal, 24px);
}
.breadcrumbs__wrap {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
.breadcrumbs__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.breadcrumbs__divider {
  padding-right: 8px;
  padding-left: 8px;
  color: #222;
}
.breadcrumbs__item {
  padding-top: 4px;
  padding-bottom: 4px;
}
.breadcrumbs__item a {
  color: #222;
}
.breadcrumbs__item a:hover {
  color: #00376e;
}
.breadcrumbs__item--disabled {
  pointer-events: none;
}
.breadcrumbs__item--disabled a {
  color: #0884ff;
}
.promo {
  position: relative;
  height: 100vh;
}
.promo a::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.promo__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('/img/bkg-table.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.promo__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('/img/bkg-newyear.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.promo__cover--item {
  background-image: url('/img/bkg-table.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.promo__img {
  position: absolute;
  right: 25%;
  bottom: 25%;
  width: 25%;
}
